import { Injectable } from '@angular/core';
import * as io from 'socket.io-client';

@Injectable()
export class SocketService {
  public socket = io('https://inventory-control-app.herokuapp.com', {
    reconnect: true,
    transports: ['websocket'],
  });
  constructor() { }

}



